@import url("./reset.css");

.bag__wrapper {
  display: flex;
}

.bag__content {
  width: 973px;
  padding: 0 48px;
}

.bag__list {
  margin-top: 34px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.bag__card-title {
  padding: 24px;
  display: flex;
  gap: 16px;
  background: #fff;
  border-radius: 21px;
}

.img__card-bag {
  width: 203px;
  height: 254px;
}

.bag__series {
  margin-top: 8px;
  margin-bottom: 16px;
  font-size: 20px;
  line-height: 27px;
  color: rgb(96, 105, 92);
}

.bag__description {
  margin-bottom: 8px;
  font-size: 20px;
  line-height: 27px;
  color: rgb(26, 31, 22);
}

.img-rating {
  margin-bottom: 38px;
}

.bag__card-price {
  width: 593px;
  display: flex;
  justify-content: space-between;
}

.price {
  font-size: 20px;
  line-height: 27px;
}
.price__currency {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.bag__counter {
  width: 111px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn__minus,
.btn__plus {
  width: 34px;
  height: 34px;
  border: none;
  background-color: transparent;
}

.counter__number {
  font-size: 20px;
  line-height: 27px;
}

.vertical__line {
  height: 921px;
  margin-top: 51px;
}

.bag {
  padding: 41px 26px 0;
  text-align: center;
}

.bag-list {
  width: 224px;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-top: 18px;
  margin-bottom: 10px;
}

.bag__button-img {
  text-align: -webkit-center;
}

.bag__img {
  width: 64px;
  height: 64px;
}

.bag__total {
  margin-bottom: 32px;
  font-size: 20px;
}

.bag__button-checkout {
  width: 209px;
  display: flex;
  justify-content: space-between;
  padding: 8px 24px 8px 24px;
  border-radius: 14px;
  background: rgb(26, 31, 22);
  font-family: Open Sans;
  font-size: 20px;
  line-height: 27px;
  color: rgb(255, 255, 255);
}
